import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { ModalDecanoComponent } from 'src/app/commons/components/modal-decano/modal-decano.component';
import { ModalDirectorComponent } from 'src/app/commons/components/modal-director/modal-director.component';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { DecanoModel } from 'src/app/commons/models/gestion-academica/decano.model';
import { DirectorModel } from 'src/app/commons/models/gestion-academica/director.model';
import { PeriodoModel } from 'src/app/commons/models/gestion-academica/periodo.model';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { UtilValidation } from 'src/app/commons/util/util.validation';
import { paginacionDecano, resetDecano } from 'src/app/store/actions/decano.actions';
import { paginacionDirector } from 'src/app/store/actions/director.actions';
import { AppState } from 'src/app/store/app.reducer';

@Component({
  selector: 'app-config-directores',
  templateUrl: './config-directores.component.html',
  styles: [
  ]
})
export class ConfigDirectoresComponent implements OnInit, OnDestroy {

  public formDecano: FormGroup;
  private _subscriptionDirector: Subscription;
  public listaDirector: DirectorModel[];
  public loaded: boolean = false;
  public loading: boolean = false;
  public decano: DirectorModel = new DirectorModel();
  public edit: boolean = false;
  @BlockUI() blockUI: NgBlockUI;

  //-----------
  public page: number = 1;
  public pageSize: number = 10;
  public totalElements: number;

  @ViewChild(ModalDirectorComponent, { static: false })
  public modalDirector: ModalDirectorComponent;


  constructor(
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
  ) {
    this.appService.pageTitle = 'Mantenedor de Decanos';

  }

  ngOnInit(): void {
    this.listarDirectores();
    this._directorState();
  }
  ngOnDestroy(): void {
    this._subscriptionDirector.unsubscribe();
  }

  private _directorState() {
    this._subscriptionDirector = this.store.select("director").subscribe(
      (state) => {
        if (state.searchPagination != null) {
          this.page = state.searchPagination.page;
          this.pageSize = state.searchPagination.records;

        }
        if (state.loaded && state.paginacionDirector) {
          this.loaded = state.loaded
          this.listaDirector = state.paginacionDirector.content;
          this.totalElements = state.paginacionDirector.totalElements;
          this.blockUI.stop();
        }


      }
    );
  }

  listarDirectores() {
    this.listaDirector = [];
    const searchPagination: SearchPagination<string> = {
      page: this.page,
      records: this.pageSize,
      seek: "",
    };
    this.blockUI.start("Cargando...");
    this.store.dispatch(paginacionDirector({ searchPagination })
    );
  }

  abrirModal(director: DirectorModel) {
    this.modalDirector.openModal(director);
  }
}
