import { EscuelaModel } from './escuela.model';

export class DirectorModel {

    public id: number = 0;
    public escuela: EscuelaModel = null;
    public nombre: string = "";
    public correo: string = "";

    constructor(fields?: {
        id?: number,
        escuela?: EscuelaModel,
        nombre?: string,
        correo?: string,
    }) {
        if (fields) { Object.assign(this, fields); }
    }
}