import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { guardarCompromiso, paginationCompromiso, resetCompromisos } from 'src/app/store/actions/compromiso.actions';
import { paginacionDirector, resetDirector } from 'src/app/store/actions/director.actions';
import { AppState } from 'src/app/store/app.reducer';
import { SearchPagination } from '../../interfaces/search.pagination';
import { DirectorModel } from '../../models/gestion-academica/director.model';
import { UsuarioSignModel } from '../../models/externo/usuario-sign.model';
import { CompromisoService } from '../../services/compromiso.service';
import { DirectorService } from '../../services/director.service';
import { UtilAlert } from '../../util/util.alert';
import { UtilValidation } from '../../util/util.validation';

@Component({
  selector: 'app-modal-director',
  templateUrl: './modal-director.component.html',
  styles: [
  ]
})
export class ModalDirectorComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;

  @ViewChild('modalDirector', { static: false })
  modalDirector: ElementRef;

  private _subscriptionCompromiso: Subscription;
  public director: DirectorModel;
  public formDirector: FormGroup;

  public buscador: String = ""
  public page: number = 1;
  public pageSize: number = 10;
  public searchPagination: SearchPagination<string>;
  public estado: string = "";
  public anio: string = "";

  public usuarioSign: UsuarioSignModel;
  public mensajeError: string;
  public estaRegistrado: Boolean = false;
  public errorSign: Boolean = false;
  public usuarioRolSign: any = null;

  constructor(
    private modalParaDirector: NgbModal,
    private store: Store<AppState>,
    private utilValidation: UtilValidation,
    public alertService: UtilAlert,
    private toastr: ToastrService,
    private compromisoService: CompromisoService,
    private directorService: DirectorService,
  ) { }

  ngOnInit(): void {
    this.newformDirector();
    this._directorState();
  }

  newformDirector() {
    this.formDirector = new FormGroup({
      usuario: new FormControl("", [Validators.required]),
      nombre: new FormControl("", [Validators.required]),
      correo: new FormControl("", [Validators.required, this.utilValidation.emailValido]),
    });
    this.formDirector.get("nombre").disable();
    this.formDirector.get("correo").disable();
  }

  openModal(director: DirectorModel) {
    this.modalParaDirector.open(this.modalDirector, { size: 'sm', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false));
    this.director = { ...director };
    this.formDirector.get("usuario").setValue(this.director.correo.replace("@uft.cl", ""));
    this.buscarSign();

  }

  cerrarModal(dimissAll = true) {
    if (dimissAll) this.modalParaDirector.dismissAll();
    this.reiniciarForm();
  }

  _directorState() {
    this._subscriptionCompromiso = this.store.select("director").subscribe((state) => {
      if (state.searchPagination) {
        this.obtenerPagination(state.searchPagination.seek.toString(), state.searchPagination.page, state.searchPagination.records);
      }
      if (state.director && state.loadedGuardado) {
        this.alertService.successSwal(`Se ha editado correctamente al director`);
        this.store.dispatch(resetDirector());
        this.cerrarModal();
        this.blockUI.stop();
      }
    });
  }

  guardarDirector() {
    debugger;
    this.formDirector.get("nombre").enable();
    this.formDirector.get("correo").enable();
    UtilValidation.setFormForValidate(this.formDirector);
    if (this.formDirector.invalid) {
      this.toastr.info("Debe ingresar correctamente todos los datos antes de guardar");
      this.formDirector.get("nombre").disable();
      this.formDirector.get("correo").disable();
      return;
    }
    if (!this.estaRegistrado) {
      if (this.usuarioSign != null) {
        this.compromisoService.saveEncargadoSign(this.usuarioSign).subscribe((value) => {
          if (value.error) {
            this.toastr.error("Se guardará el director en el sistema, pero no se ha podido guardar en el sign");
            this.toastr.error(value.mensaje);
          } else {
            this.usuarioSign = value;
          }
        });
      }
    } else {
      console.log("darRolEncargado")
      this.darRolEncargado()
    }
    this.blockUI.start("Guardando...")
    this.setearDirector();
    this.directorService.save(this.director).subscribe((value) => {
      this.alertService.successSwal(`Se ha editado correctamente el director de la escuela`)
      this.listarCompromisos();
      this.cerrarModal();
      this.blockUI.stop();
    }, (error) => {
      this.toastr.error("No se ha logrado guardar el encargado")
      this.blockUI.stop();
    })

    this.formDirector.get("nombre").disable();
    this.formDirector.get("correo").disable();
  }

  obtenerPagination(buscador: string, page: number, pageSize: number) {
    this.buscador = buscador;
    this.page = page;
    this.pageSize = pageSize;
    this.searchPagination = {
      seek: buscador,
      page: page,
      records: pageSize
    }
    console.table(this.searchPagination)

  }

  listarCompromisos() {
    this.store.dispatch(paginacionDirector({ searchPagination: this.searchPagination }))
    return;
  }

  buscarAD() {
    this.mensajeError = null;
    const encargadoEmail: string = this.formDirector.get("usuario").value;
    this.compromisoService.getEncargadoByUsuarioAD(encargadoEmail).subscribe((value) => {
      console.log(value)
      if (value.error) {
        if (value.mensaje == "No se encontró el usuario") {
          this.toastr.warning(encargadoEmail + " no se encuentra registrado en Active Directory", "Problemas al Encontrar a " + encargadoEmail)
          this.errorSign = true;
        }
      }
      else if (value.resultado != null) {
        this.usuarioSign = value.resultado;
        this.formDirector.get("nombre").setValue(this.usuarioSign.nombre);
        this.formDirector.get("correo").setValue(this.usuarioSign.usuarioEmail + "@uft.cl");
        this.estaRegistrado = false;
        this.errorSign = false;
      }
    })
  }

  buscarSign() {
    if (this.formDirector.get("usuario").value == "") {
      return;
    }
    this.formDirector.get("nombre").setValue("");
    this.formDirector.get("correo").setValue("");
    const encargadoEmail: string = this.formDirector.get("usuario").value;
    this.mensajeError = null;
    this.compromisoService.getEncargadoByUsuarioEmail(encargadoEmail).subscribe((value) => {
      if (value.error) {
        this.buscarAD()
      } else {
        console.log(value)
        this.usuarioSign = value.resultado
        this.formDirector.get("nombre").setValue(this.usuarioSign.nombre);
        this.formDirector.get("correo").setValue(this.usuarioSign.usuarioEmail + "@uft.cl");
        this.estaRegistrado = true;
        this.errorSign = false;
      }
    })
  }

  darRolEncargado() {
    this.directorService.saveRoleSign(this.usuarioSign).subscribe((value) => {
      if (value.error) {
        this.toastr.error("No se ha podido dar acceso al encargado a la plataforma", "Error:")
        console.log(value)
        this.blockUI.stop();
      } else {
        console.log(value)
      }
    });
  }

  setearDirector() {
    this.director.correo = this.formDirector.get("correo").value;
    this.director.nombre = this.formDirector.get("nombre").value;
  }

  reiniciarForm() {
    this.formDirector.get("usuario").setValue("");
    this.formDirector.get("nombre").setValue("");
    this.formDirector.get("correo").setValue("");
    this.formDirector.get("usuario").markAsUntouched();
    this.formDirector.get("nombre").markAsUntouched();
    this.formDirector.get("correo").markAsUntouched();
  }

  changeUsuario() {
    this.formDirector.get("nombre").setValue("");
    this.formDirector.get("correo").setValue("");
  }
}
